.tips {
    background-image: url("../../images/IMG_0670.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    color: #fff;
}

.tips-header {
    font-size: 3rem;
    margin-top: 475px;
    margin-left: 50px;
}

.mid-tips {
    background-color: #84b686;
    margin: 50px;
}

.tips-content {
    margin-left: 100px;
    margin-right: 100px;
    margin-bottom: 100px;
    display: flex;
    color: #fff;
}

.tips-p {
    margin-top: -50px;
    margin-bottom: 30px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
    float: left;
}

.image-tips {
    height: 500px;
    float: right;
}

@media only screen and (max-width: 1366px) {
    .tips {
        background-attachment: scroll;
    }
}

@media screen and (max-width: 1000px) {
    .tips-content {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        object-fit: contain;
        padding: 10px;
    }

    .mid-tips h1 {
        margin-top: 60px;
        margin-left: -100px;
        margin-right: -100px;
        margin-bottom: -20px;
        font-size: 20px;
    }
    .mid-tips p {
        font-size: 15px;
        margin-left: -100px;
        margin-right: -100px;
        margin-bottom: 20px;
    }
}
